import meter1 from "../assets/img/meter1.svg";
import meter2 from "../assets/img/meter2.svg";
import meter3 from "../assets/img/meter3.svg";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import arrow1 from "../assets/img/arrow1.svg";
import arrow2 from "../assets/img/arrow2.svg";
import colorSharp from "../assets/img/color-sharp.png";
import angular from "../assets/img/Angular.png";
import react from "../assets/img/pngwing.com.png";
import redux from "../assets/img/Redux.png";
import node from "../assets/img/Node.png";
import python from "../assets/img/python.png";

export const Skills = () => {
  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3,
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
    },
  };
  const imageStyle = {
    height: "135px",
    width: "fit-content",
  };

  return (
    <section className="skill" id="skills">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="skill-bx wow zoomIn">
              <h2>Skills</h2>
              {/* <p>These are some of the skills that I have</p> */}
              <Carousel
                responsive={responsive}
                infinite={true}
                className="owl-carousel owl-theme skill-slider"
              >
                <div className="item">
                  <img style={imageStyle} src={react} alt="Image" />
                  <h5>React</h5>
                </div>
                <div className="item">
                  <img style={imageStyle} src={angular} alt="Image" />
                  <h5>Angular</h5>
                </div>
                <div className="item">
                  <img style={imageStyle} src={node} alt="Image" />
                  <h5>Node</h5>
                </div>
                <div className="item">
                  <img style={imageStyle} src={python} alt="Image" />
                  <h5>Python</h5>
                </div>
                <div className="item">
                  <img style={imageStyle} src={redux} alt="Image" />
                  <h5>Redux</h5>
                </div>
              </Carousel>
            </div>
          </div>
        </div>
      </div>
      {/* <img className="background-image-left" src={colorSharp} alt="Image" /> */}
    </section>
  );
};
